<template>
  <div class="asp">
    <div class="asp-nav"></div>
    <div class="asp-content">
      <div class="asp-form">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="">
            <el-input v-model="formInline.PositionName" placeholder=""></el-input>
          </el-form-item> 
          <el-form-item label="">
            <el-input v-model="formInline.Company" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="ID">
            <el-input v-model="formInline.CompanyId" placeholder="ID"></el-input>
          </el-form-item>
          <el-form-item style="float:right;margin:0 5% 0 0">
            <el-button @click="Ressetting()"></el-button>
            <el-button type="primary" @click="Inquiry()"></el-button>
          </el-form-item>
        </el-form>
        <div class="asp-table">
          <el-tabs v-model="activeName" type="card" @tab-click="handleClicktab">
            <el-tab-pane label="" name="first">
              <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
              >
                <el-table-column type="selection" width="100"></el-table-column>
                <el-table-column prop="positionId" label="ID"></el-table-column>
                <el-table-column prop="positionName" label=""></el-table-column>
                <!-- <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column> -->
                <el-table-column prop="createdTime" sortable label="" show-overflow-tooltip><template slot-scope="scope">{{ scope.row.createdTime | formatDate}}</template></el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                    <el-button type="text"  @click="DefineFirst(scope.row)" size="small"></el-button>
                    <el-tooltip class="item" effect="light" placement="bottom-start">
                      <div slot="content">
                        <span>
                          <i style="color:#faad14;margin:0 5px 0 0" class="el-icon-warning-outline"></i>
                        </span>
                        <span></span>
                        <div style="font-size:10px">
                          <el-checkbox-group v-model="checkList">
                            <el-checkbox label=""></el-checkbox>
                            <br />
                            <el-checkbox label=""></el-checkbox>
                            <br />
                            <el-checkbox label=""></el-checkbox>
                            <br />
                            <el-checkbox label=""></el-checkbox>
                            <br />
                            <el-checkbox label=""></el-checkbox>
                          </el-checkbox-group>
                          <br />
                          <el-button style="width:50px;height:20px;font-size:10px;padding:0 0"></el-button>
                          <el-button
                            style="width:50px;height:20px;font-size:10px;padding:0 0"
                            type="primary"
                            @click="Define(scope.row)"
                          ></el-button>
                        </div>
                      </div>
                      <el-button type="text" size="small"></el-button>
                    </el-tooltip>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="page.current"
                  :page-sizes="page.pageSizeOpts"
                  :page-size="page.pageSize"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="page.total">
                </el-pagination> 
            </el-tab-pane>
            <el-tab-pane label="" name="second">
              <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
              >
                <el-table-column type="selection" width="100"></el-table-column>
                <el-table-column prop="positionId" label="ID"></el-table-column>
                <el-table-column prop="positionName" label=""></el-table-column>
                <el-table-column prop="companyId" label="" show-overflow-tooltip></el-table-column>
                <!-- <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column> -->
                <el-table-column prop="createdTime" sortable label="" show-overflow-tooltip><template slot-scope="scope">{{ scope.row.createdTime | formatDate}}</template></el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="page.current"
                  :page-sizes="page.pageSizeOpts"
                  :page-size="page.pageSize"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="page.total">
                </el-pagination> 
            </el-tab-pane>
            <el-tab-pane label="" name="third">
              <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
              >
                <el-table-column type="selection" width="100"></el-table-column>
                <el-table-column prop="positionId" label="ID"></el-table-column>
                <el-table-column prop="positionName" label=""></el-table-column>
                <el-table-column prop="companyId" label="" show-overflow-tooltip></el-table-column>
                <!-- <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column> -->
                <el-table-column prop="createdTime" sortable label="" show-overflow-tooltip><template slot-scope="scope">{{ scope.row.createdTime | formatDate}}</template></el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="page.current"
                  :page-sizes="page.pageSizeOpts"
                  :page-size="page.pageSize"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="page.total">
                </el-pagination> 
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      page: {
        total: 0,
        pageSize: 10,
        current: 1,
        pageSizeOpts: [10,20,30]
            },
      formInline:{
        PositionName:'',
        Company:'',
        CompanyId:''
      },
      checkList: [],
      dialogVisible: false,
      activeName: "first",
      currentPage: 4,
      tableData: [],
      multipleSelection: [],
      positionId:'',
      id:''
    };
  },
  methods: {
    //
    Define(tab, event) {
      this.$http.put(`/reviewed/position/${tab.positionId}/position/${tab.id}/notPass`,{reason:this.checkList[0]}).then(res => {
          if (res.data.code == 200) {
            this.ReviewCompany()
          }
        }).catch(error =>{
          // this.$message({
          //       message:error.response.data.message,
          //       type: 'error'
          //     })
        });
    },
    //
    DefineFirst(tab,event) {
      this.$http.put(`/reviewed/position/${tab.positionId}/position/${tab.id}/pass`).then(res => {
          if (res.data.code == 200) {
            this.ReviewCompany()
          }
        }).catch(error =>{
          // this.$message({
          //       message:error.response.data.message,
          //       type: 'error'
          //     })
        });
    },
    //tabs 
    handleClicktab (tab, event) {
      let tabValue =  tab.name
      if(tabValue === 'first') {
        this.$http.get('/reviewed/position/info',{params:{reviewedState:1}}).then(res => {
          if (res.data.code == 200) {
            this.tableData = res.data.data.list
            this.page.total = res.data.data.total
          }
        }).catch(error =>{
          this.$message({
                message:error.response.data.message,
                type: 'error'
              })
        });
      }else if(tabValue === 'second') {
        this.$http.get('/reviewed/position/info',{params:{reviewedState:2}}).then(res => {
          if (res.data.code == 200) {
            this.tableData = res.data.data.list
            this.page.total = res.data.data.total
          }
        }).catch(error =>{
          this.$message({
                message:error.response.data.message,
                type: 'error'
              })
        });
      }else {
        this.$http.get('/reviewed/position/info',{params:{reviewedState:3}}).then(res => {
          if (res.data.code == 200) {
            this.tableData = res.data.data.list
            this.page.total = res.data.data.total
          }
        }).catch(error =>{
          this.$message({
                message:error.response.data.message,
                type: 'error'
              })
        });
      }
    },
    //
    ReviewCompany() {
      this.$http.get('/reviewed/position/info',{params:{reviewedState:1}}).then(res => {
          if (res.data.code == 200) {
            this.tableData = res.data.data.list
            this.page.total = res.data.data.total
          }
        }).catch(error =>{
          // this.$message({
          //       message:error.response.data.message,
          //       type: 'error'
          //     })
        });
    },
    //
    DialogAffirm() {
      this.$http
        .put(`/reviewed/position/${this.companyId}/position/${this.id}/rePass`, {reason:this.textarea
        })
        .then(res => {
          if (res.data.code == 200) {
            this.dialogVisible = false
            this.$http.get('/reviewed/company/cert',{params:{reviewedState:3}}).then(res => {
                if (res.data.code == 200) {
                  this.tableData = res.data.data.list
                  this.page.total = res.data.data.total
                }
              }).catch(error =>{
                // this.$message({
                //       message:error.response.data.message,
                //       type: 'error'
                //     })
              });
          } else {
          }
        })
        .catch(error => {
          this.$message(error.response.data.message);
        });
    },
    handleClick(tab, event) {
      this.$router.push({ path:'/JobDetail',query: {
              thisId:tab.id,
              thatId:tab.positionId
            } });
    },
    //
    Ressetting() {
      this.formInline.PositionName = "";
      this.formInline.Company = "";
      this.formInline.CompanyId = "";
    },
    //
    Inquiry() {
      this.$http
        .post("", {
          
        })
        .then(res => {
          if (res.data.code == 200) {
          } else {
          }
        })
        .catch(error => {
          this.$message(error.response.data.message);
        });
    },
    keep() {
      this.dialogVisible = true;
    },
    handle(tab, event) {
      console.log(tab, event);
     this.$router.push({path:'/Bteam/Enterdetail'})
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    }
  },
  mounted: function() {},
  updated: function() {},
  created() {
    this.ReviewCompany()
  }
};
</script>
<style scoped>
.asp {
  width: 100%;
  background: #F8F8F8;
  border:1px solid #F8F8F8;
}
.asp-nav {
  margin: 40px 0 0 20px;
  font-size: 18px;
  color:#2A2A2A;
  text-align: left;
}
.asp-content {
  width: 100%;
  margin: 20px 0 0 0;
  border: 1px solid #efeff3;
  background: #efeff3;
}
.asp-form {
  width: 98%;
  background: #ffffff;
  margin: 10px 0 0 1%;
}
.el-form-item {
  padding: 10px 0 0 20px;
}
.el-select {
  width: 203px;
}
.asp-table {
  width: 98%;
  background: #ffffff;
  margin: 10px 0 0 1%;
}
.el-pagination {
  text-align: center; 
  margin:20px auto;
}
.el-checkbox__label {
  font-size: 10px;
}
.el-checkbox {
  margin: 10px 0 0 20px;
}
</style>



